.about__accordeon{
    width: 1023px;
    display: flex;
    flex-direction: column;
    gap: 30px;

    @media (max-width :1240px){
        width: 860px;
    }
    @media (max-width :1070px){
        width: 560px;
    }
    @media (max-width : 610px){
        width: 390px;
    }

    @media (max-width :425px){
        width: 100%;
    }
}

.open{
    background-color: #F6F6F6;
    border-radius: 10px;
}

.collapse__header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 34px;
    padding: 10px 20px 10px 15px;
    background-color: #FF6060;
    border-radius: 10px;

    @media (max-width : 425px){
        font-size:12px;
        height: 10px;
        border-radius: 5px;
    }

    &__title{
        margin: 0;
        color: #FFFFFF;
        font-weight: 500;
        
    }

    img{
        cursor: pointer;
        
        @media (max-width : 425px){
            width: 25px;
            height: 25px;
        }
    }

    &__button{
        transition: transform 300ms ease-out;
        
        &--open{
            transform: rotate(180deg);
            transition: transform 300ms ease-out;
        }
    }
}

.collapse__content{
    visibility: hidden;
    height: 0;
    opacity: 0;
    overflow: hidden;
    padding: 0 20px;
    color: #FF6060;
    font-size: 18px;
    font-weight: 400;
    list-style: none;
    transition: all 300ms ease-in;

    @media (max-width : 768px){
        font-size: 16px;
    }

    @media (max-width : 425px){
        font-size:12px;
    }
}

.open .collapse__content {
    visibility: visible;
    height: auto;
    overflow: visible;
    padding: 20px;
    opacity: 1;
}